/**
 * @file 这里进行一些对底层的封装，底层包括：
 * 1. 中间件机制；
 * 2. 对 perf API 的繁杂处理；
 * 3. 对 weblogger 上报的封装；
 * @author guwanpo@kuaishou.com
 */
import type { HttpWeblogInstance } from '@/types/index';
import type { Base } from '../../proto/base';
import type {
    KVPair, RadarOptions, RadarMonitor,
} from './interface';
import type { RadarPluginConstructor, RadarPlugin } from './plugin';

import { BasePlugin } from '../base';
import { KEY } from './const';
import { radarPoint, normalizeURL } from './utils';

// const REPORT_THRESSHOLD = 60 * 1000;
// const PAGELOG_API = 'sendRadarLog';

export class RadarRoot extends BasePlugin {
    declare weblog: HttpWeblogInstance;
    private plugins: RadarPlugin[] = [];
    private logQueue: KVPair[] = [];
    private queueConfig = {
        wait: 1500,
        maxBatchLength: 200,
    };
    private batchTimer: any = 0;
    private radarSessionId: string = '';
    private currentUrlPackage!: HttpWeblogInstance['currentUrlPackage'];
    public options: RadarOptions;
    public isUsingLegacy: boolean | null = null;
    public isUsingDetachedReport: boolean | null = null;
    // 默认忽略列表
    public ignoreList: string[] = ['https://web-trace.ksapisrv.com/ktrace/collect'];
    constructor(options: RadarOptions) {
        super();
        radarPoint('radarCreated-firstReport');
        radarPoint('radarCreated-onload');
        this.options = options;
        this.queueConfig = {
            ...this.queueConfig,
            ...(this.options ? this.options.queue : {}),
        };

        this.ignoreList = this.ignoreList.concat(normalizeURL(this.options && this.options.ignoreList));
    }

    protected use(plugin: RadarPluginConstructor<RadarOptions>, ctx: RadarMonitor) {
        this.plugins.push(new plugin(ctx, this.options));
    }

    override apply(weblog: HttpWeblogInstance) {
        this.weblog = weblog;
        // @ts-ignore
        this.ignoreList.push(this.weblog.logger.url);
        //  生成唯一sessionid
        this.radarSessionId = this.nanoId();
        this.created();
    }

    created() {
        this.plugins.forEach(plugin => {
            plugin.created();
        });
    }
    destroy() {
        this.plugins.forEach(plugin => {
            plugin.destroy();
        });
    }

    flush() {
        if (!this.options) {
            return;
        }

        if (this.logQueue.length <= 0) {
            return;
        }

        const h5_extra_attr = JSON.stringify({
            // @ts-ignore
            ...this.weblog.commonPackage.getH5ExtraAttr(),
            url: location.href,
            hash: location.hash,
        });

        const { commonPackage: { app_package } } = this.weblog;
        // const commonParams = {
        //     project_id: this.options.projectId,
        //     app_version_name: app_package.app_version_name,
        //     // @ts-ignore
        //     url_package: this.currentUrlPackage && this.currentUrlPackage.toJSON(),
        //     h5_extra_attr,
        // };
        // 桥上报，目前不支持，注释掉
        // this.addRadarStatEvent({
        //     removeStashedLog: [],
        //     sendImmediate: true,
        //     customData: {
        //         data: [this.logQueue],
        //         ...commonParams,
        //     },
        // });
        const commonLogData = {
            project_id: this.options.projectId,
            radar_session_id: this.radarSessionId,
            h5_extra_attr,
        };

        this.weblog.collect('CUSTOM', {
            key: KEY,
            value: {
                ...commonLogData,
                data: this.logQueue,
            },
            // @ts-ignore
            currentUrlPackage: this.currentUrlPackage && this.currentUrlPackage.toJSON(),
            // @ts-ignore
            referUrlPackage: this.referUrlPackage && this.referUrlPackage.toJSON(),
        });
        this.logQueue = [];
    }

    decorateLog(kv: KVPair) {
        if (!this.currentUrlPackage) {
            this.currentUrlPackage = this.weblog.currentUrlPackage;
        }

        // 由于 url 是日志公参，因此 url 发生变化时直接冲刷日志
        if (this.currentUrlPackage.page !== this.weblog.currentUrlPackage.page) {
            this.flush();
            this.currentUrlPackage = this.weblog.currentUrlPackage;
        }
    }

    logCollect(kv: KVPair) {
        if (!this.options) {
            return;
        }

        if (this.batchTimer) {
            clearTimeout(this.batchTimer);
        }

        this.decorateLog(kv);
        this.logQueue.push(kv);
        if (this.logQueue.length > this.queueConfig.maxBatchLength) {
            this.flush();
            return;
        }

        this.batchTimer = setTimeout(() => {
            this.flush();
        }, this.queueConfig.wait);
    }

    addRadarStatEvent(log: any) {
        try {
            const { Utils: { yoda } } = this.weblog;
            if (!yoda) { return false; }
            yoda.sendRadarLog(log);
            return true;
        } catch (e) {
            return false;
        }
    }

    /**
     * 生成nano id
     * 目前radarsessionid生成逻辑与weblog相同，防止weblog修改，雷达自身保存一份
     * @return {string} 16长度的字符串
     */
    private nanoId() {
        const ts = ((Math.random() * 1e9) >>> 0);
        const randomer = [];
        const seed = '0123456789ABCDEF';
        for (let i = 0; i < 7; i++) {
            randomer.push(seed.charAt(Math.random() * 16));
        }

        return ts + randomer.join('');
    }
}
