/**
 * @file kds Radar 插件
 * @author guwanpo@kuaishou.com
 */

import type { RadarMonitor, RadarOptions } from './interface';
import { RadarRoot } from './base';
import { RADAR_KEY } from './const';
import { error } from '@/util/console';

export default class Radar extends RadarRoot implements RadarMonitor {
    static override key = 'radar';

    constructor(options: RadarOptions) {

        super(options);

        if (!this.validOption()) {
            return;
        }

    }

    // 采样控制
    static samplingControl(options?: RadarOptions) {
        if (!options) {
            return false;
        }

        const { sampling } = options;
        const rand = Math.random();

        return rand < sampling;
    }

    // 用户自定义系列指标
    public async fmp(time?: number) {
        const radarFmp = time || Date.now();
        const { yoda } = this.weblog.Utils;
        const kdsPerf = yoda && await yoda.getKDSWebviewLoadPerf();
        console.log(kdsPerf, 'kdsPerf');
        if (!kdsPerf) {
            error('[Radar]未拿到kds性能数据');
            return;
        }
        // @ts-ignore
        const fmp = radarFmp - kdsPerf.startLoad;

        this.logCollect({
            key: RADAR_KEY.CUSTOM,
            value: {
                fmp,
            },
        });
        this.flush();
    }

    validOption() {
        if (!this.options) {
            // 未传入参数默认不开启
            return false;
        }

        const { sampling, projectId } = this.options;
        if (typeof projectId !== 'string') {
            error('[error 303]', '[Radar] 必须配置 projectId 属性，类型为 string!');
            return false;
        }
        if (sampling > 1 || sampling < 0) {
            error('[error 304]', '[Radar] 采样率 sampling 必须为 0~1 之间的数字');
            return false;
        }

        return true;
    }
}
